*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;

}

:root{
  --color-bg: rgba(19, 18, 18, 0.726);
  --color-btn: rgb(235, 188, 194);
  --color-primary: rgba(149, 235, 199, 0.486) ;
  --color-light: #fff;
  --color-border: rgba(204, 245, 226, 0.644);


  --transition: all 400ms ease;

  --container-width-lg:75%;
  --container-width-md:80%;
  --container-width-ms:90%;
}

html{
  scroll-behavior: smooth;
}
::-webkit-scrollbar{
  display: none;
}

body{
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  background-color: var(--color-bg);
  color: white;
  line-height:1.2;
  background: var("./assets/bg2-image.jpg");
}

/********* Genral-Styles*********/
.container{
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,h2,h3,h4,h5{
  font-weight: 500;
}
h1{
  font-size: 2.5rem;
}

section{
  margin-top: 6rem;
}

section > h1, section > h4{
  text-align: center;
  color: aliceblue;

}
section > h2{
  color: dodgerblue;
  margin-bottom: 3rem;
}
.text-light{
  color: rgb(75, 58, 58);
}
a{
  color: black;
  transition: var(--transition);
}
a:hover{
  color: brown;
}

.btn{
  width:max-content;
  display: inline-block;
  color: rgb(216, 199, 199);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid lightgray;
  transition: var(--transition);
}
.btn:hover{
  background: var(--color-btn);
  color: var(--color-bg);
  border-color: transparent;
}
.btn-primary{
  background:var(--color-btn);
  color: black;
}
img{
  display: block;
  width: 100%;
  object-fit: cover;
}

/* *****************Media-Queries(Medium-Devices)***********/

@media screen and (max-width:1020px){
  .container{
    width: var(--container-width-md);
  }
  section{
    margin-top: 6rem;
  }
}

/* *****************Media-Queries(Small-Devices)***********/
@media screen and (max-width:600px){
  .container{
    width: var(--container-width-ms);
  }
  section > h2{
    margin-bottom: 2rem;
  }
}
/* *****************Media-Queries(Small-Devices)***********/
@media screen and (max-width:370px){
  .container{
  width: var(--container-width-ms);
  }
}



