.about-container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 12%;
}

.about-me{
    width: 20rem;
    height: 22rem;
    margin-top: 2rem;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, rgb(41, 98, 100) );
    display: grid;
    place-items: center;
}
.about-me-image{
    width: 19rem;
    height: 22rem;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about-me-image:hover{
    transform: rotate(0);
} 
.about-cards{
    margin-top: 2rem;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.2rem;
}
.about-card{
    background:var(--color-primary);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}
.about-card:hover{
    background: transparent;
    border-color: rgba(204, 245, 226, 0.644);
    cursor: default;
}
.about-icons{
    color:black;
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about-card h4{
    font-size: 0.95rem;
}
.about-card small{
    font-size: 0.7rem;
    color: white;
}
.about-content p{
    margin: 2rem 0 2.6rem;
    color:var(--color-light);
}

/* *****************Media-Queries(Medium-Devices)**********/
@media screen and (max-width:1024px) {
 .about-container{
    grid-template-columns: 1fr;
    gap: 0;
 } 

 .about-me{
    width: 50%;
    margin: 2rem auto 4rem;
 }
 .about-content p{
    margin: 1rem 0 1.5rem;
 }
}

/* *****************Media-Queries(Small-Devices)***********/
 @media screen and (max-width:600px) {
 .about-me{
    width: var(--container-width-sm);
    margin: 1.5rem auto 2.5rem 
 }
 .about-cards{
   grid-template-columns: 1fr 1fr;
   gap: 1rem;
 }

 .about-content{
    text-align: center;
 }
 .about-content p{
    margin: 1.5rem 0;
 }
} 